import {useEffect, useState} from 'react';
import {createId} from '../lib/createId';
import {useUpdate} from './useUpdate';


const useTags = () => {
  const [tags, setTags] = useState<{ id: number; name: string; iconName: string }[]>([]);
  useEffect(() => {
    let localTags = JSON.parse(window.localStorage.getItem('tags') || '[]');
    if (localTags.length === 0) {
      localTags = [
        {
          'id': createId(),
          'name': '',
          'iconName': 'shopping'
        },
        {
          'id': createId(),
          'name': '',
          'iconName': 'happy'
        },
        {
          'id': createId(),
          'name': '',
          'iconName': 'eat'
        },
        {
          'id': createId(),
          'name': '',
          'iconName': 'phone'
        },
        {
          'id': createId(),
          'name': '',
          'iconName': 'pet'
        },
        {
          'id': createId(),
          'name': '',
          'iconName': 'transportation'
        },
        {
          'id': createId(),
          'name': '',
          'iconName': 'study'
        },
        {
          'id': createId(),
          'name': '',
          'iconName': 'travel'
        },
        {
          'id': createId(),
          'name': '',
          'iconName': 'financing'
        },
        {
          'id': createId(),
          'name': '',
          'iconName': 'othercome'
        },
        {
          'id': createId(),
          'name': '',
          'iconName': 'parttimejob'
        },
        {
          'id': createId(),
          'name': '',
          'iconName': 'salary'
        },
        {
          'id': createId(),
          'name': '',
          'iconName': 'transfer'
        }
      ];
    }
    setTags(localTags);

  }, []); // 

  useUpdate(() => {
    window.localStorage.setItem('tags', JSON.stringify(tags));
  }, tags);
  const findTag = (id: number) => tags.filter(tag => tag.id === id)[0];
  const findTagIndex = (id: number) => {
    let result = -1;
    for (let i = 0; i < tags.length; i++) {
      if (tags[i].id === id) {
        result = i;
        break;
      }
    }
    return result;
  };
  const updateTag = (id: number, {name}: { name: string }, {iconName}: { iconName: string }) => {
    setTags(tags.map(tag => tag.id === id ? {id, name: name, iconName: iconName} : tag));
  };
  const deleteTag = (id: number) => {
    setTags(tags.filter(tag => tag.id !== id));
  };
  const addTag = () => {
    const tagName = window.prompt('');
    if (tagName !== null && tagName !== '') {
      setTags([...tags, {id: createId(), name: tagName, iconName: 'othercome'}]);
    }
  };
  const getName = (id: number) => {
    const tag = tags.filter(t => t.id === id)[0];
    return tag ? tag.name : '';
  };
  const getIconName = (id: number) => {
    const tag = tags.filter(t => t.id === id)[0];
    return tag ? tag.iconName : '';
  };
  return {tags, getName, getIconName, addTag, setTags, findTag, updateTag, findTagIndex, deleteTag};
};

export {useTags};